//拓展$.prototype
	$.fn.extend({
		LizToggleOne:function(str1,str2) {
			if ($(this).hasClass(str1)) {
				$(this).removeClass(str1).addClass(str2);
			}else if ($(this).hasClass(str2)) {
				$(this).removeClass(str2).addClass(str1);
			}
		},
		hasNoClass:function (str) {
			var $this=$(this);
			var $that;
			$this.each(function(){
				if (!$(this).hasClass(str)) {
					$that=$(this);
				};
			});
			return $that;
		}
	});
	


